import { Group } from '@visx/group';
import { Grid } from '@visx/grid';
import { BarGroup } from '@visx/shape';
import { AxisBottom } from '@visx/axis';
import { scaleBand, scaleLinear, scaleOrdinal } from '@visx/scale';
import { LegendOrdinal } from '@visx/legend';
import { useTooltip, useTooltipInPortal, defaultStyles } from '@visx/tooltip';
import { localPoint } from '@visx/event';
import dayjs from 'dayjs';

import { Typography } from 'components/ui';

import type { SeriesPoint } from '@visx/shape/lib/types';

type LabelName = 'value1' | 'value2' | 'value3';

export type Item = {
  date: string;
  value1: string;
  value2: string;
  value3: string;
};

type TooltipData = {
  bar: SeriesPoint<Item>;
  key: LabelName;
  index: number;
  height: number;
  width: number;
  x: number;
  y: number;
  color: string;
};

export type GroupedBarProps = {
  width?: number;
  height?: number;
  backgroundColor?: string;
  hasGrid?: boolean;
  margin?: { top: number; right: number; bottom: number; left: number };
  events?: boolean;
  data: Item[];
};

const lightBlue = '#85ceff';
export const mediumBlue = '#009aff';
const darkBlue = '#00568f';

const defaultMargin = { top: 0, right: 0, bottom: 0, left: 0 };
const tooltipStyles = {
  ...defaultStyles,
  minWidth: 80,
  minHeight: 40,
  backgroundColor: 'rgba(0,0,0,0.9)',
  color: 'white'
};

const formatDate = (date: string) => dayjs(date).format('MM월 DD일');

const GroupedBar = ({
  width = 400,
  height = 300,
  backgroundColor = 'transparent',
  hasGrid = true,
  margin = defaultMargin,
  events = false,
  data
}: GroupedBarProps) => {
  // accessors
  const getDate = (d: Item) => d.date;

  const keys = Object.keys(data[0]).filter((d) => d !== 'date') as LabelName[];

  // const temperatureTotals = data.reduce((allTotals, currentDate) => {
  //   const totalTemperature = keys.reduce((dailyTotal, k) => {
  //     dailyTotal += Number(currentDate[k]);
  //     return dailyTotal;
  //   }, 0);
  //   allTotals.push(totalTemperature);
  //   return allTotals;
  // }, [] as number[]);

  // scales
  const dateScale = scaleBand<string>({
    domain: data.map(getDate),
    padding: 0.2
  });

  const cityScale = scaleBand<string>({
    domain: keys,
    padding: 0.1
  });
  const tempScale = scaleLinear<number>({
    domain: [0, Math.max(...data.map((d) => Math.max(...keys.map((key) => Number(d[key])))))],
    // domain: [0, Math.max(...temperatureTotals)]
    nice: true
  });
  const colorScale = scaleOrdinal<string, string>({
    domain: keys,
    range: [lightBlue, mediumBlue, darkBlue]
  });

  let tooltipTimeout: number;

  const xMax = width - margin.left - margin.right;
  const yMax = height - margin.top - margin.bottom;

  dateScale.rangeRound([0, xMax]);
  cityScale.rangeRound([0, dateScale.bandwidth()]);
  tempScale.range([yMax, 0]);

  const { tooltipOpen, tooltipLeft, tooltipTop, tooltipData, hideTooltip, showTooltip } =
    useTooltip<TooltipData>();

  const { containerRef, TooltipInPortal } = useTooltipInPortal({
    scroll: true
  });

  if (!width || !height) {
    return null;
  }

  return (
    <div
      // ref={containerRef}
      className="qb-grouped-bar-chart flex flex-col gap-5 items-center relative"
    >
      <svg ref={containerRef} width={width} height={height}>
        <rect x={0} y={0} width={width} height={width} fill={backgroundColor} rx={0} />
        {hasGrid && (
          <Grid
            top={margin.top}
            left={margin.left}
            xScale={dateScale}
            yScale={tempScale}
            width={xMax}
            height={yMax}
            stroke="black"
            strokeOpacity={0.1}
            xOffset={dateScale.bandwidth() / 2}
          />
        )}
        <Group top={margin.top} left={margin.left}>
          <BarGroup
            data={data}
            keys={keys}
            height={yMax}
            x0={getDate}
            x0Scale={dateScale}
            x1Scale={cityScale}
            yScale={tempScale}
            color={colorScale}
          >
            {(barGroups) =>
              barGroups.map((barGroup) => (
                <Group key={`bar-group-${barGroup.index}-${barGroup.x0}`} left={barGroup.x0}>
                  {barGroup.bars.map((bar) => (
                    <rect
                      key={`bar-group-bar-${barGroup.index}-${bar.index}-${bar.value}-${bar.key}`}
                      x={bar.x}
                      y={bar.y}
                      width={bar.width}
                      height={bar.height}
                      fill={bar.color}
                      rx={0}
                      onClick={() => {
                        if (!events) return;
                        const { key, value } = bar;
                        alert(JSON.stringify({ key, value }));
                      }}
                      onMouseMove={(event) => {
                        if (tooltipTimeout) clearTimeout(tooltipTimeout);
                        // const top = event.clientY;
                        // const left = bar.x;
                        const left = bar.x + bar.width / 2;
                        const eventSvgCoords = localPoint(event);

                        showTooltip({
                          // tooltipData: getDate(bar.key),
                          tooltipData: bar.key,
                          tooltipTop: eventSvgCoords?.y,
                          tooltipLeft: left
                        });
                      }}
                      onMouseLeave={() => {
                        tooltipTimeout = window.setTimeout(() => {
                          hideTooltip();
                        }, 300);
                      }}
                    />
                  ))}
                </Group>
              ))
            }
          </BarGroup>
        </Group>
        <AxisBottom
          top={yMax + margin.bottom}
          left={margin.left}
          scale={dateScale}
          stroke={mediumBlue}
          hideTicks
          tickFormat={formatDate}
          hideAxisLine
          tickLabelProps={() => ({
            fill: mediumBlue,
            fontSize: 15,
            textAnchor: 'middle'
          })}
        />
      </svg>
      <div>
        <LegendOrdinal scale={colorScale} direction="row" />
      </div>

      {tooltipOpen && tooltipData && (
        <TooltipInPortal
          key={Math.random()}
          top={tooltipTop}
          left={tooltipLeft}
          style={tooltipStyles}
        >
          <div style={{ color: colorScale(tooltipData.key as string) }}>
            <strong>{tooltipData.key}</strong>
          </div>
          <div>{tooltipData.bar.data[tooltipData.key]}℉</div>
          <div>
            <Typography>{formatDate(getDate(tooltipData.bar.data as Item))}</Typography>
          </div>
        </TooltipInPortal>
      )}
    </div>
  );
};

export default GroupedBar;
